<template>
    <v-app-bar app color="indigo" light>
        <!-- Fuerza los elementos hacia la derecha -->
        <v-spacer></v-spacer>
        <v-btn to="/" text>
            <v-icon>mdi-account-plus</v-icon>
            <!-- MR-2 = MARGIN RIGHT 8PX -->
            <span class="mr-2">Nuevo usuario</span>
        </v-btn>
        <v-btn to="/users" text>
            <v-icon>mdi-account-multiple</v-icon>
            <span class="mr-2">Mostrar usuarios</span>
        </v-btn>
        <v-btn @click="logout" text>
            <v-icon>mdi-logout</v-icon>
            <span class="mr-2">Log out</span>
        </v-btn>
    </v-app-bar>
</template>

<script>
import { getAuth, signOut } from "firebase/auth";

export default {
    name: 'navbar-comp',
    // props: {},
    data: function () {
        return {}
    },
    // computed: {},
    methods: {
        // Desconecta la sesión existente
        logout() {
            const auth = getAuth()
            // Método de Firebase Auth
            signOut(auth)
                .then(() => {
                    // Redirige en caso de un log out exitoso
                    this.$router.push('/login')
                })
                .catch((error) => {
                    // Imprime el error, si lo hubiera
                    console.log(error)
                })
        },
    },
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    // -- End Lifecycle Methods
}
</script>

