import Vue from 'vue'
import Vuex from 'vuex'
// Firebase
import { db } from '@/firebase/firebase.js'
import { collection, getDocs } from 'firebase/firestore'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    listaUsuarios: [],
  },
  getters: {
  },
  mutations: {
    // Limpia la lista de usuarios
    RESET_USERS(state) {
      state.listaUsuarios = []
    },
    // Agrega los usuarios al array LISTAUSUARIOS
    GET_USERS(state, user){
      state.listaUsuarios.push({
        id: user.id,
        nombre: user.data().nombre,
        email: user.data().email
      })
    },
  },
  actions: {
    async fetchUsers({ commit }) {
      // Una promesa que obtiene un objeto de la colección USERS
      let querySnapShot = await getDocs(collection(db, 'users'))
      // Limpia preventivamente el array
      commit('RESET_USERS')
      // Ciclo forEach
      querySnapShot.forEach((doc) => {
        // Toma el mutation GET_USERS y le pasa DOC como argumento
        commit('GET_USERS', doc)
      })
    },
  },
  modules: {
  }
})
