  <v-app>
    <NavBar v-if="esRutaPrivada"></NavBar>
    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
import NavBar from '@/components/NavBar.vue';

export default {
  name: 'component-name',
  // props: {},
  data: function () {
    return {};
  },
  computed: {
    esRutaPrivada() {
      // Verifica si existe el atributo en ROUTER
      // Si existe, muestra la barra
      if (this.$route.meta.private) return true
      // Si no existe, la oculta.
      return false
    }
  },
  // methods: {},
  // watch: {},
  components: {
    NavBar,
  },
  // mixins: [],
  // filters: {},
  // -- Lifecycle Methods
  // -- End Lifecycle Methods
}
</script>

