<template>
  <div>
    <h1>Editar usuarios</h1>
    <h2>(Firestore)</h2>
    <v-form ref="form" v-model="valid" lazy-validation>
      <v-container>
        <v-row>
          <v-col md="6">
            <v-text-field v-model="usuarios.nombre" :counter="10" :rules="nameRules" label="Nombre" required>
            </v-text-field>
          </v-col>
          <v-col md="6">
            <v-text-field v-model="usuarios.email" :rules="emailRules" label="E-mail" required></v-text-field>
          </v-col>
          <v-col>
            <v-btn :disabled="!valid" color="success" class="mr-4" @click="editUser()">
              Editar usuario
            </v-btn>
            <v-btn color="error" @click="dialog = true">Borrar usuario</v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
    <v-dialog v-model="dialog" width="500">
      <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          ¿Deseas eliminar este usuario?
        </v-card-title>

        <v-divider></v-divider>

        <v-card-actions>
          <v-btn color="red" text @click="deleteUser">Eliminar</v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false"> Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { db } from '@/firebase/firebase.js'
import { doc, getDoc, updateDoc, deleteDoc } from 'firebase/firestore'

export default {
  name: 'edit-user',
  // Toma el id que viene de la pagina anterior
  props: ['idUser'],
  data: function () {
    return {
      usuarios: {
        nombre: '',
        email: ''
      },
      dialog: false,
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'El nombre es requerido',
        v => (v && v.length <= 10) || 'El nombre debe ser menos de 10 caracteres',
      ],
      email: '',
      emailRules: [
        v => !!v || 'El E-mail es requerido',
        v => /.+@.+\..+/.test(v) || 'El E-mail debe ser válido',
      ],
    }
  },
  // computed: {},
  methods: {
    // Obtiene la info usando el ID
    async fetchUserById() {
      const docRef = doc(db, 'users', this.idUser)
      let response = await getDoc(docRef)
      console.log(response.data())
      this.usuarios = response.data()
    },
    async editUser() {
      // Valida los campos
      this.$refs.form.validate()
      // Si no es válido, retorna nada
      if (!this.valid) return
      // Pasa el nuevo array al documentos USERS
      try {
        let user = this.usuarios
        user.id = this.idUser
        await updateDoc(doc(db, 'users', user.id), user)
        // Redigire a la lista de usuarios
        this.$router.push('/users')
      } catch (error) {
        console.log(error)
      }
    },
    // Borra el usuario usando como base el ID
    async deleteUser() {
      try {
        await deleteDoc(doc(db, 'users', this.idUser))
        this.$router.push('/users')
      } catch (error) {
        console.log(error)
      }
    },
  },
  // watch: {},
  // components: {},
  // mixins: [],
  // filters: {},
  // -- Lifecycle Methods
  created() {
    // Carga en el formulario los datos obtenidos 
    this.fetchUserById()
  }
  // -- End Lifecycle Methods
}
</script>

