<template>
	<v-container>
		<v-row justify="center">
			<v-col md="5">
				<h2>Crear nuevo usuario</h2>
				<v-form ref="form" v-model="valid" lazy-validation>
					<v-text-field v-model="email" :rules="emailRules" label="Correo" required></v-text-field>

					<v-text-field v-model="pass" :rules="passRules" label="Contraseña" required type="password">
					</v-text-field>

					<v-btn :disabled="!valid" color="success" class="mr-4" @click="create">
						Crear
					</v-btn>

					<v-btn color="error" class="mr-4" @click="reset">
						Limpiar Formulario
					</v-btn>
					<v-btn color="warning" class="mr-4" @click="$router.push('/login')">
						Iniciar sesión
					</v-btn>
				</v-form>
			</v-col>
		</v-row>
	</v-container>
</template>

<script>
import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";

export default {
	name: 'create-view',
	// props: {},
	data: function () {
		return {
			valid: true,
			pass: '',
			passRules: [
				(v) => !!v || 'Contraseña es requerido',
				(v) =>
					(v && v.length <= 6) || 'Contraseña debe máximo 6 caracteres',
			],
			email: '',
			emailRules: [
				(v) => !!v || 'Correo es requerido',
				(v) => /.+@.+\..+/.test(v) || 'Correo debe ser valido',
			],
		}
	},
	// computed: {},
	methods: {
		validate() {
			this.$refs.form.validate()
		},
		reset() {
			this.$refs.form.reset()
		},
		create() {
			// Valida el formulario
			this.validate()
			// Si la validación da falso, escapa de la función
			if (this.valid == false) return

			const auth = getAuth();
			// Método de auth para crear usuarios, recibe (getAuth, email, contraseña)
			createUserWithEmailAndPassword(auth, this.email, this.pass)
				.then((userCredential) => {
					// Inicia sesión e imprime por consola información del nuevo usuario
					const user = userCredential.user;
					console.log(user);
					// Redirige al HOME
					this.$router.push('/')
				})
				.catch((error) => {
					const errorCode = error.code;
					const errorMessage = error.message;
					console.log(errorCode);
					console.log(errorMessage);
				});
		},
	},
	// watch: {},
	// components: {},
	// mixins: [],
	// filters: {},
	// -- Lifecycle Methods
	// -- End Lifecycle Methods
}
</script>

