<template>
  <div>
    <h1>Añadir usuarios</h1>
    <h2>(Firestore)</h2>
    <v-form ref="form" v-model="valid" lazy-validation>
      <v-container>
        <v-row>
          <v-col md="6">
            <v-text-field v-model="usuarios.nombre" :counter="10" :rules="nameRules" label="Nombre" required>
            </v-text-field>
          </v-col>
          <v-col md="6">
            <v-text-field v-model="usuarios.email" :rules="emailRules" label="E-mail" required></v-text-field>
          </v-col>
          <v-col>
            <v-btn :disabled="!valid" color="success" class="mr-4" @click="addUser()">
              Añadir nuevo usuario
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
  </div>
</template>

<script>
import { db } from '@/firebase/firebase.js'
import { collection, addDoc } from 'firebase/firestore'

export default {
  name: 'component-name',
  // props: {},
  data: function () {
    return {
      usuarios: {
        nombre: '',
        email: ''
      },
      valid: true,
      name: '',
      // Reglas para validar el nombre
      nameRules: [
        v => !!v || 'El nombre es requerido',
        v => (v && v.length <= 10) || 'El nombre debe ser menos de 10 caracteres',
      ],
      email: '',
      // Reglas para validar el email
      emailRules: [
        v => !!v || 'El E-mail es requerido',
        v => /.+@.+\..+/.test(v) || 'El E-mail debe ser válido',
      ],
    }
  },
  // computed: {},
  methods: {
    async addUser() {
      try {
        // Crea una constante con la promesa de agregar los usuarios al documento USERS
        const docRef = await addDoc(collection(db, "users"), this.usuarios);
        console.log("Documento creado con el ID: ", docRef.id);
        // Redirige a la página Lista de Usuarios
        this.$router.push('/users')
      } catch (e) {
        console.error("Error al agregar el documento: ", e);
      }
    },
  },
}
  // watch: {},
  // components: {},
  // mixins: [],
  // filters: {},
  // -- Lifecycle Methods
  // -- End Lifecycle Methods
</script>

