<template>
	<v-container>
		<v-row justify="center">
			<v-col md="5">
				<h2>Login</h2>
				<v-form ref="form" v-model="valid" lazy-validation>
					<v-text-field v-model="email" :rules="emailRules" label="Correo" required></v-text-field>

					<v-text-field v-model="pass" :rules="passRules" label="Contraseña" required type="password">
					</v-text-field>

					<v-btn :disabled="!valid" color="success" class="mr-4" @click="login">
						Ingresar
					</v-btn>

					<v-btn color="error" class="mr-4" @click="reset">
						Limpiar Formulario
					</v-btn>

					<v-btn color="warning" class="mr-4" @click="$router.push('/create')">
						Crear nuevo usuario
					</v-btn>
				</v-form>
			</v-col>
		</v-row>
	</v-container>
</template>

<script>
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
export default {
	name: 'login-view',
	// props: {},
	data: function () {
		return {
			valid: true,
			pass: '',
			passRules: [
				(v) => !!v || 'Contraseña es requerido',
				(v) =>
					(v && v.length <= 6) || 'Contraseña deb tener al menos 6 caracteres',
			],
			email: '',
			emailRules: [
				(v) => !!v || 'Correo es requerido',
				(v) => /.+@.+\..+/.test(v) || 'Correo debe ser valido',
			],
		}
	},
	// computed: {},
	methods: {
		validate() {
			this.$refs.form.validate()
		},
		reset() {
			this.$refs.form.reset()
		},
		login() {
			// Valida el formulario
			this.validate()
			// Si el formulario no es válido, escapa de la función
			if (this.valid == false) return

			const auth = getAuth()
			// Método de auth para iniciar sesión, recibe (getAuth, email, contraseña)
			signInWithEmailAndPassword(auth, this.email, this.pass)
				.then((userCredential) => {
					// Inicia sesión satisfactoriamente e imprime info del usuario
					const user = userCredential.user
					console.log(user)
					// Redirige al HOME
					this.$router.push('/')
				})
				.catch((error) => {
					const errorMessage = error.message
					alert(errorMessage)

				})
		},
	},
	// watch: {},
	// components: {},
	// mixins: [],
	// filters: {},
	// -- Lifecycle Methods
	// -- End Lifecycle Methods
}
</script>

