import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import {getAuth, onAuthStateChanged} from 'firebase/auth'
Vue.config.productionTip = false

const auth = getAuth() 

// Establece un observador de Auth para saber si hay un usuario logueado o no
// Evita que el usuario acceda a urls protegidas a través de la barra de navegación
onAuthStateChanged(auth,()=>{
  new Vue({
    router,
    store,
    vuetify,
    render: h => h(App)
  }).$mount('#app')
})