import Vue from 'vue'
import VueRouter from 'vue-router'
import AddUser from '../views/AddUser.vue'
import GetUser from '@/views/GetUsers.vue'
import EditUser from '@/views/EditUser.vue'
import Login from '@/views/Login.vue'
import Create from '@/views/Create.vue'
import { getAuth } from 'firebase/auth'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'add-user',
    component: AddUser,
    meta: {
      private: true
    }
  },
  {
    path: '/users',
    name: 'get-users',
    component: GetUser,
    meta: {
      private: true
    }
  },
  {
    path: '/users/:idUser',
    name: 'edit-user',
    component: EditUser,
    props: true,
    meta: {
      private: true
    }
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
  },
  {
    path: '/create',
    name: 'create',
    component: Create,
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  const auth = getAuth()
  let user = auth.currentUser

  // Toma el valor META.PRIVATE de la ruta DE DESTINO y lo asigna a RUTA_PRIVADA
  let ruta_privada = to.meta.private

  // Si RUTA_PRIVADA es TRUE, y USER es FALSE (no autenticado) 
  if (ruta_privada && !user) {
    // Redirige a LOGIN
    next('/login')
  }
  // Si RUTA_PRIVADA es igual a UNDEFINED y USER es TRUE (autenticado)
  else if (ruta_privada == undefined && user) {
    // Redirige al HOME. Evita que el usuario acceda al login infinito.
    next('/')
  }
  else {
    next()
  }
})

export default router
